import React,{ PropTypes } from 'react'
//,active,span,active,,
//constcreateClass...
const Link = ({active,children,onClick})=>{
	if(active){
		return (
			<span>{children}</span>
		)
	}

	return (
		<a href='#' onClick={
			(e)=>{
				e.preventDefault()
				onClick()
			}
		} >
		</a>
	)
}
Link.propTypes = {
	active:PropTypes.bool.isRequired,
	children:React.PropTypes.node.isRequired,
	onClick:PropTypes.func.isRequired	
}

export default Link


